import { showNotificationFail } from './notification';

function fetchCountriesName(searchCountry) {
  const url = `https://restcountries.eu/rest/v2/name/${searchCountry}`;

  return fetch(url)
    .then(res => res.json())
    .catch(error => {
      console.log(error);
      showNotificationFail();
    });
}

export default fetchCountriesName;
